export default {
	data() {
		return {
			list:[],
		}
	},
	onLoad:function(){
		uni.showLoading({
			title: '加载中...',
			mask: true
		});
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.list=res.data;
			},
			fail: () => {},
			complete: () => {
				uni.hideLoading();
			}
		});
	},
	methods: {
		showInfo(item) {
			console.log(item)
			uni.navigateTo({
				url: '../info/info?id='+item.post_id
			});
		}
	},
}
